import React from "react";
import { Link, useHistory } from "react-router-dom";
import edcanImg from "./assets/EDCAN_LOGO.svg"

const Foot = () => {
  const history = useHistory();
  return (
    <footer>
      <hr />
      <div className="py-8 bg-white content-center text-justify text-gray-500">
        <div className="flex flex-col sm:flex-row flex-wrap justify-center w-full">
          <Link className="w-32 flex mx-auto sm:mx-0" to="/">
            <img src={edcanImg} alt="에드캔 로고" />
          </Link>
          <div className="mt-4 sm:mt-0 text-center sm:text-left px-8 flex items-center">
            <div className="text-sm mx-auto sm:mx-0 font-light">
              서울 용산구 원효로97길 33-4, 선린인터넷고등학교
              <br />
              Copyright ©EDCAN. All rights reserv
              <div onClick={() => {history.push('/login')}} className="inline">
                ed
              </div>
              .
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Foot;
