import React from 'react';
import { useHistory } from 'react-router-dom';

export default function Login() {

    const history = useHistory();

    const check = () => {
        if(document.getElementById("pw").value===process.env.REACT_APP_EDCAN_PW_KEY){
            history.push('/upload');
        }
    }

    return (<div className="flex items-center bg-black opacity-90 w-full h-screen">
    <div className="text-center w-full text-lg">
      <input id="pw" className="text-gray-200  font-extralight  bg-white px-6 py-3 rounded-xl" placeholder="비밀번호를 입력하세요" />
      <button onClick={check} className="text-gray-300  font-extralight ml-2 bg-white px-6 py-3 rounded-xl">확인</button>
    </div>
  </div>);
}